import { mapActions } from 'vuex'
import { upyunUrl } from '../../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      oJson: '',
      id: 0
    }
  },
  onLoad (options) {
    this.id = options.id
    this.getCourseInfoFn()
  },
  methods: {
    ...mapActions([
      'getCourseInfo'
    ]),
    // 获取课程详情
    getCourseInfoFn () {
      let data = {
        that: this,
        id: this.id
      }
      uni.showLoading({ title: 'Loading' })
      this.getCourseInfo(data).then((res) => {
        this.oJson = res
        uni.hideLoading()
      })
    },
    // 跳转页面
    onSkipPageFn (oJson, state) {
      let url = ''
      if (state) {
        switch (oJson.v_type) {
          case 0:
            url = `/pages/subject/subjectMusic/index?sid=${oJson.id}`
            break
          case 1:
            oJson['special_id'] = oJson.id
            url = `/pages/subject/subjectVideo/index?oJson=${encodeURIComponent(JSON.stringify(oJson))}`
            break
        }
      } else {
        url = oJson
      }
      uni.navigateTo({
        url
      })
    }
  }
}
